.page {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#home {
  background-image: url(../img/space.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

#titel {
  font-size: 6rem;
  z-index: 1000;
}

@keyframes hover {
  0% {top: 48vh;}
  50% {top: 50vh;}
  100% {top: 48vh;}
}

#spaceman {
  position: fixed;
  height: 40vh;
  width: auto;
  left: 73vw;
  top: 28vh;
  transform: translate(-50%, -50%);
  -webkit-animation: hover 5s infinite ;
  -moz-animation: hover 5s infinite;
  -o-animation: hover 5s infinite;
  animation: hover 5s infinite;
  animation-timing-function: ease-in-out;
  -webkit-animation-timing-function: ease-in-out;
}

@media only screen and (max-width: 2000px) {

#home {
  position: fixed;
  left: 0;
  right: 0;
}

#titel {
  font-size: 8vw;
  position: fixed;
  top: 20vh;
  width: 100vw;
  text-align: center;
  margin: 0;
  padding: 0;
}

#spaceman {
  transform: translate(-50%, -50%);
  left: 50vw;
}

@keyframes hover {
  0% {top: 60vh;}
  50% {top: 62vh;}
  100% {top: 60vh;}
}
}
